  .grid__section-B {
    grid-area: B; }
  .grid__section-C {
    grid-area: C; }
  .grid__section-D {
    grid-area: D; }
  .grid__section-E {
    grid-area: E; }
  .grid__section-F {
    grid-area: F; }

